.loginbox {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.loginbox__backdrop {
  height: 100vh;
  width: 100vw;
  background-color: var(--warna-hitam);
  opacity: 0.5;
}
.loginbox__container {
  opacity: 1;
  visibility: visible;
  border-radius: 1.25rem;
  width: 90%;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 28.125rem;
  background-color: var(--warna-putih);
}
@media screen and (min-width: 37.5rem) {
  .loginbox__container {
    width: 34.375rem;
  }
}
.loginbox__container h1 {
  text-align: center;
}
.loginbox__container img {
  cursor: pointer;
  margin: 1.25rem;
  width: 5%;
  transform: rotate(45deg);
  transition: transform 0.5s ease;
}
.loginbox__container img:hover {
  transform: rotate(135deg);
}
.loginbox__container p {
  margin-top: 0.75rem;
  text-align: center;
  font-size: 0.75rem;
}
.loginbox__container p span {
  cursor: pointer;
  font-weight: 700;
  transition: all 0.5s ease;
}
.loginbox__container p span:hover {
  border-radius: 3.125rem;
  padding: 0.1875rem 0.875rem 0.1875rem 0.875rem;
  color: var(--warna-putih);
  background-color: var(--warna-hitam);
}
.loginbox__container form {
  width: 80vw;
  max-width: 25rem;
  display: flex;
  flex-direction: column;
  margin: 1rem auto 5rem auto;
}
@media screen and (min-width: 37.5rem) {
  .loginbox__container form {
    width: 25rem;
  }
}
.loginbox__container form label {
  margin-bottom: 0.375rem;
}
.loginbox__container form input {
  width: 80vw;
  max-width: 25rem;
  padding: 0.375rem 0 0.375rem 0;
  border: solid var(--warna-hitam) 0.0625rem;
  border-radius: 3.125rem;
  margin-bottom: 1rem;
}
@media screen and (min-width: 37.5rem) {
  .loginbox__container form input {
    width: 25rem;
  }
}
.loginbox__container .button {
  display: flex;
  justify-content: center;
  align-items: center;
}
.loginbox__container .button__login button {
  max-width: 25rem;
  width: 80vw;
  cursor: pointer;
  border: solid 0.0625rem black;
  border-radius: 3.125rem;
  padding: 0.5rem 0;
  font-weight: 500;
  font-size: 1.125rem;
  color: var(--warna-putih);
  background-color: black;
  transition: all 0.5s ease;
}
.loginbox__container .button__login button:hover {
  color: var(--warna-hitam);
  background-color: var(--warna-putih);
  border: solid 0.0625rem var(--warna-hitam);
}