@use "../util/" as *;

.loginbox {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  &__backdrop {
    height: 100vh;
    width: 100vw;
    background-color: var(--warna-hitam);
    opacity: 0.5;
  }
  &__container {
    opacity: 1;
    visibility: visible;
    border-radius: rem(20);
    width: 90%;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: rem(450);
    background-color: var(--warna-putih);
    @include breakpoint(medium) {
      width: rem(550);
    }
    h1 {
      text-align: center;
    }
    img {
      cursor: pointer;
      margin: rem(20);
      width: 5%;
      transform: rotate(45deg);
      transition: transform 0.5s ease;
    }
    img:hover {
      transform: rotate(135deg);
    }
    p {
      margin-top: rem(12);
      text-align: center;
      font-size: rem(12);
      span {
        cursor: pointer;
        font-weight: 700;
        transition: all 0.5s ease;
      }
      span:hover {
        border-radius: rem(50);
        padding: rem(3) rem(14) rem(3) rem(14);
        color: var(--warna-putih);
        background-color: var(--warna-hitam);
      }
    }
    form {
      width: 80vw;
      max-width: rem(400);
      display: flex;
      flex-direction: column;
      margin: 1rem auto 5rem auto;
      @include breakpoint(medium) {
        width: rem(400);
      }
      label {
        margin-bottom: rem(6);
      }
      input {
        width: 80vw;
        max-width: rem(400);
        padding: rem(6) 0 rem(6) 0;
        border: solid var(--warna-hitam) rem(1);
        border-radius: rem(50);
        margin-bottom: 1rem;
        @include breakpoint(medium) {
          width: rem(400);
        }
      }
    }
    .button {
      display: flex;
      justify-content: center;
      align-items: center;
      &__login {
        button {
          max-width: rem(400);
          width: 80vw;
          cursor: pointer;
          border: solid rem(1) black;
          border-radius: rem(50);
          padding: rem(8) 0;
          font-weight: 500;
          font-size: rem(18);
          color: var(--warna-putih);
          background-color: black;
          transition: all 0.5s ease;
        }
        button:hover {
          color: var(--warna-hitam);
          background-color: var(--warna-putih);
          border: solid rem(1) var(--warna-hitam);
        }
      }
    }
  }
}
